%h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

%background-image {
  background-position: center;
  background-repeat: no-repeat;
}

%testimonials-line {
  position: absolute;
  content: "";
  width: 139px;
  height: 56px;
  border-radius: 500px;
  -webkit-transform: rotate(-60deg);
  transform: rotate(-60deg);
  z-index: 1;
  transition: all 0.3s;
}
